import slugify from 'slugify'

slugify.extend({
  '#': 'tag',
  '@': 'em',
  '+': 'mais',
  '-': 'menos',
  $: 'real',
  '&': 'e',
})

export function slugifyConfigured(val) {
  return slugify(val, {
    replacement: '_',
    lower: true,
    remove: /[*~.()'"!?´`^:]/,
  })
}

/**
 * @param {String} key
 * @param {SvelteStore} store
 */
export function useLocalStorage(key, store) {
  const val = window.localStorage.getItem(`__GLOBAL_STORE_${key}`)
  if (val) {
    store.set(JSON.parse(val))
  }

  store.subscribe(state => {
    window.localStorage.setItem(`__GLOBAL_STORE_${key}`, JSON.stringify(state))
  })

  return store
}
