import { writable, derived, readable } from 'svelte/store'
import { useLocalStorage } from '../helpers'
import { urlData } from './router'

import { projectList } from '../api/project'
import { templateList } from '../api/template'

export const listProjects = readable([], projectList)
export const listTemplates = writable([], templateList)
export const listFields = writable([])

export const currentProject = derived(
  [urlData, listProjects],
  ([$urlData, $listProjects]) =>
    $listProjects.find(p => p.slug === $urlData.query.project || '') || {}
)

export const currentTemplate = derived(
  [urlData, listTemplates],
  ([$urlData, $listTemplates]) =>
    $listTemplates.find(p => p.slug === $urlData.query.template || '') || {}
)

export const currentField = derived(
  [urlData, listFields],
  ([$urlData, $listFields]) =>
    $listFields.find(p => p.slug === $urlData.query.field || '') || {}
)

export const recentProject = useLocalStorage('recent-project', writable({}))
