export const getFirebase = () => firebase

export const getUser = () => {
  const auth = firebase.auth()
  return auth.currentUser
}

export const isSigned = () => {
  return !!getUser()
}

export const signOut = () => {
  return getFirebase()
    .auth()
    .signOut()
}

export const getUserAsync = () => {
  return new Promise((resolve, reject) => {
    getFirebase()
      .auth()
      .onAuthStateChanged(user => {
        if (user) resolve(user)
        else reject(new Error('user_not_logged'))
      })
  })
}

export const isSignedAsync = async () => {
  return !!(await getUserAsync())
}

export const getTokenAsync = () => {
  return new Promise((resolve, reject) => {
    getUserAsync()
      .then(u => u.getIdToken())
      .then(resolve)
      .catch(reject)
  })
}

export const startFirebaseUI = async id => {
  return new Promise(resolve => {
    if (!window.firebaseui) return resolve()
    else {
      const uiConfig = {
        signInFlow: 'popup',
        signInSuccessUrl: window.location.origin,
        signInOptions: [
          {
            provider: getFirebase().auth.GoogleAuthProvider.PROVIDER_ID,
            authMethod: 'https://accounts.google.com',
            clientId: CLIENT_ID,
            customParameters: {
              prompt: 'select_account',
            },
          },
          {
            provider: getFirebase().auth.EmailAuthProvider.PROVIDER_ID,
            requireDisplayName: true,
          },
        ],
        callbacks: {
          uiShown: function() {
            resolve()
          },
        },
        credentialHelper: window.firebaseui.auth.CredentialHelper.GOOGLE_YOLO,
      }

      let ui = window.firebaseui.auth.AuthUI.getInstance()
      if (!ui) ui = new window.firebaseui.auth.AuthUI(getFirebase().auth())
      ui.start('#' + id, uiConfig)
    }
  })
}
