import { get } from 'svelte/store'
import { getTokenAsync } from '../helpers/auth'
import { urlData } from '../stores/router'
import { snackList } from '../stores/main'
import { fetchBase } from './index'

function loadList(set) {
  const project = get(urlData).query.project
  if (!project) return
  getTokenAsync()
    .then(token => {
      return fetchBase(`/template/list?project=${project}`, {
        token,
      })
    })
    .then(([status, result]) => {
      if (status !== 200) {
        console.error(status, result)
        return
      }
      if (result.status === 'success') {
        console.log('%ctemplate-list', 'color:#16F; text-transform:uppercase;')
        console.log(result.data)
        set(
          result.data.map(r => ({
            name: r.name,
            slug: r.slug,
            site: r.site,
            fields: r.fields,
            order: r.order,
            lastUpdate: r.lastUpdate,
            created: r.created,
          }))
        )
      } else {
        snackList.add({ text: 'Não foi possível carregar a lista de modelos' })
      }
    })
    .then(() => {})
    .catch(err => console.error('ERROR', err))
}

export function templateList(set) {
  set([])
  if (!window.listenTemplateList) {
    window.listenTemplateList = true
    window.addEventListener('reload-template-list', () => {
      snackList.add({ text: 'Carregando lista de modelos!' })
      loadList(set)
    })
  }
  loadList(set)
}

export function addTemplate(template) {
  return new Promise((resolve, reject) => {
    const project = get(urlData).query.project
    if (!project) return reject()

    getTokenAsync()
      .then(token => {
        return fetchBase('/template', {
          token,
          method: 'POST',
          data: {
            ...template,
            project,
          },
        })
      })
      .then(([status, result]) => {
        if (status !== 200) {
          console.error(status, result)
          reject()
        }
        if (result.status === 'success') {
          window.dispatchEvent(new CustomEvent('reload-template-list'))
          resolve(result.data)
        } else {
          snackList.add({ text: 'Não foi possível criar um novo modelo!' })
          reject()
        }
      })
      .catch(reject)
  })
}
