import { writable } from 'svelte/store'

export const searchTitle = writable('')

function createSnackList() {
  let { set, update, subscribe } = writable([])
  let index = 0
  const res = {
    subscribe,
    set,
    clean: () => set([]),
    /**
     * @param {{text:String}} obj
     */
    add: obj => {
      obj.__id = index++
      update(v => [...v, obj])
      if (index > 10000) index = 0

      setTimeout(() => {
        res.remove(obj.__id)
      }, obj.timeout || 2000)
    },
    remove: id => update(v => v.filter(e => e.__id !== id)),
  }

  return res
}

export const snackList = createSnackList()
