import { writable } from 'svelte/store'

export const urlData = writable({
  segment: '',
  query: {},
})

export function setRouter(s, q) {
  const qmap = new URLSearchParams(q)
  let resQ = {}
  for (let qel of qmap.entries()) {
    resQ[qel[0]] = qel[1]
  }
  urlData.set({
    segment: s || '',
    query: resQ || {},
  })
}
