import { getTokenAsync } from '../helpers/auth'
import { snackList } from '../stores/main'
import { fetchBase } from './index'

function loadList(set) {
  getTokenAsync()
    .then(token => {
      return fetchBase('/project/list?sort=-lastUpdate.date', {
        token,
      })
    })
    .then(([status, result]) => {
      if (status !== 200) {
        console.error(status, result)
        return
      }
      if (result.status === 'success') {
        console.log('%cproject-list', 'color:#16F; text-transform:uppercase;')
        console.log(result.data)
        set(
          result.data.map(r => ({
            name: r.name,
            slug: r.slug,
            primaryColor: r.primaryColor,
            secondaryColor: r.secondaryColor,
            properties: r.properties,
            lastUpdate: r.lastUpdate,
            created: r.created,
          }))
        )
      } else {
        snackList.add({ text: 'Não foi possível carregar a lista de projetos' })
      }
    })
    .then(() => {})
    .catch(err => console.error('ERROR', err))
}

export function projectList(set) {
  if (!window.listenProjectList) {
    window.listenProjectList = true
    window.addEventListener('reload-project-list', () => {
      snackList.add({ text: 'Carregando lista de projetos!' })
      loadList(set)
    })
  }
  loadList(set)
}

export function addProject(project) {
  return new Promise((resolve, reject) => {
    getTokenAsync()
      .then(token => {
        return fetchBase('/project', {
          token,
          method: 'POST',
          data: project,
        })
      })
      .then(([status, result]) => {
        if (status !== 200) {
          console.error(status, result)
          reject()
        }
        if (result.status === 'success') {
          window.dispatchEvent(new CustomEvent('reload-project-list'))
          resolve(result.data)
        } else {
          snackList.add({ text: 'Não foi possível criar um novo projeto!' })
          reject()
        }
      })
      .catch(reject)
  })
}
