// Stores
import { setRouter, urlData } from '../stores/router'

import Projects, * as ProjectsAppBar from './Projects.svelte'
import Templates, * as TemplatesAppBar from './Templates.svelte'
import Auth, * as AuthAppBar from './Auth.svelte'

const routerMap = ['auth', 'projects', 'templates', 'users']

// Mapeamento de rotas
export default function(router) {
  router.on(function(q) {
    setRouter('', q)
  })

  routerMap.forEach(el => {
    router.on(el, function(_, q) {
      setRouter(el, q)
    })
  })

  router
    .notFound(function() {
      setRouter('404', '')
    })
    .resolve()

  urlData.subscribe(val => {
    let q = Object.entries(val.query)
      .map(e => e.join('='))
      .join('&')

    let url = val.segment

    if (q) {
      url += '?' + q
    }

    router.pause()
    router.navigate(url)
    router.resume()
  })
}

export function getPage(segment) {
  switch (segment) {
    case 'auth':
      return Auth
    case 'users':
    case 'templates':
      return Templates
    case 'projects':
    default:
      return Projects
  }
}

export function getAppBarContent(segment) {
  switch (segment) {
    case 'auth':
      return AuthAppBar
    case 'users':
    case 'templates':
      return TemplatesAppBar
    case 'projects':
    default:
      return ProjectsAppBar
  }
}
